import React from 'react'
import { useDispatch, useSelector } from "react-redux";
import {
	Form,
	Select,
} from 'antd';

import { DEFAULT_VALUE } from '../content-web-constants.js';
import {
	replacejscssfile,
	chipSeriesToChipType,
	setGlobalVariables,
} from '../content-web-commonLib.js';
import { setChipType } from "../store/slices/r_chipType";
import { set_partNumber_package } from "../store/slices/r_partNumber_package";
import { set_partNumber_type } from "../store/slices/r_partNumber_type";
import { setSnippetName } from "../store/slices/r_snippetName";
import { set_cfg_perFunctions } from "../store/slices/r_cfg_perFunctions";

const { PART_NUMBER } = DEFAULT_VALUE;

const ChipSeries = (props) => {
	const r_chipType = useSelector((state) => state.r_chipType);
	const dispatch = useDispatch();

	const initializeAll = () => {
		// g_groupIds = {};
		// g_recordedIds = {};
		// g_perControls = {};
		setGlobalVariables('g_groupIds', {});
		setGlobalVariables('g_recordedIds', {});
		setGlobalVariables('g_perControls', {});
	};

	// From buildChipTypeSelect()
	const handleChangeChipSeries = (value) => {
		var newPackage = PART_NUMBER[value].package;
		var newType = PART_NUMBER[value].type;
		var oldfilename = 'NUC_' + r_chipType + '_Content.js';
		var newfilename = 'NUC_' + chipSeriesToChipType(value) + '_Content.js';
		replacejscssfile(oldfilename, newfilename, 'js', function(){
			initializeAll();
			dispatch(setChipType(value));
			dispatch(set_partNumber_package(newPackage));
			dispatch(set_partNumber_type(newType));
			dispatch(set_cfg_perFunctions([]));
			dispatch(setSnippetName(''));
		});
	}

	return (
		<Form layout="vertical">
			<Form.Item label={'Chip Series'}>
				<Select
					defaultValue={r_chipType}
					onChange={handleChangeChipSeries}
				>
					{g_chipTypes.map((chipType) =>
						<Select.Option key={chipType} value={chipType}>
							{chipType}
						</Select.Option>)
					}
				</Select>
			</Form.Item>
		</Form>
	);
};

export default ChipSeries;