// g_partNumber_type
import { createSlice } from "@reduxjs/toolkit";
import { setGlobalVariables } from "../../content-web-commonLib";

const initialState = "M252_C";

const r_partNumber_type = createSlice({
    name: "r_partNumber_type",
    initialState,
    reducers: {
        set_partNumber_type(state, action) {
            state = action.payload;
            setGlobalVariables("g_partNumber_type" , state);
            return state;
        }
    }
});

export const { set_partNumber_type } = r_partNumber_type.actions;
export default r_partNumber_type.reducer;
