import React from 'react';
import ReactDOM from 'react-dom/client';
import { Provider } from 'react-redux';
import store from "./store";
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

import Toolbar from './components/Toolbar';
import ChipSeries from './components/ChipSeries';
import PartNo from './components/PartNo';
import PeripheralFunctions from './components/PeripheralFunctions';
import LoadPeripheralTxt from './components/LoadPeripheralTxt';

const topToolBar = ReactDOM.createRoot(document.getElementById('topToolBar'));
topToolBar.render(
  <Provider store={store}>
    <Toolbar/>
  </Provider>
);

const ChipType_Peripheral = ReactDOM.createRoot(document.getElementById('ChipType_Peripheral'));
ChipType_Peripheral.render(
  <Provider store={store}>
    <ChipSeries />
  </Provider>
);

const MCU_Peripheral = ReactDOM.createRoot(document.getElementById('MCU_Peripheral'));
MCU_Peripheral.render(
  <Provider store={store}>
    <PartNo />
  </Provider>
);

const rootTree_Peripheral = ReactDOM.createRoot(document.getElementById('rootTree_Peripheral'));
rootTree_Peripheral.render(
  <Provider store={store}>
    <PeripheralFunctions />
  </Provider>
);

const CodeGeneratorContainer = ReactDOM.createRoot(document.getElementById('CodeGeneratorContainer'));
CodeGeneratorContainer.render(
  <Provider store={store}>
    <LoadPeripheralTxt />
  </Provider>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
