import React from 'react'
import { useDispatch, useSelector } from "react-redux";
import {
	Layout,
	Tree,
} from 'antd';
import { DatabaseFilled } from '@ant-design/icons';

import {
	getPropertyNames,
	setGlobalVariables,
} from '../content-web-commonLib.js';

import { setSnippetName } from "../store/slices/r_snippetName";
import { set_cfg_perFunctions } from "../store/slices/r_cfg_perFunctions";

import './PeripheralFunctions.css';

const { Header, Content } = Layout;

const PeripheralFunctions = (props) => {
	const r_partNumber_package = useSelector((state) => state.r_partNumber_package);
	const r_partNumber_type= useSelector((state) => state.r_partNumber_type);
	const r_cfg_perFunctions = useSelector((state) => state.r_cfg_perFunctions);
	const dispatch = useDispatch();

	// From buildPerFunctionTree()
	const getUsablePeripheralFunc = () => {
		var partNumber = r_partNumber_package.slicePriorToX('(');
		let perFunctions = [],
			unusedPerFunction,
			unusedObjectArray = [],
			unusedObjectProperties = [],
			unusedObject = {};
	
		Object.assign(perFunctions, NUTOOL_PER.g_cfg_perFunctions)
		if (perFunctions.length === 0) {
			return;
		}

		// find chip type
		setGlobalVariables("g_partNumber_type" , r_partNumber_type);
	
		// trim some unused perFunctions if exists
		if (typeof (NUTOOL_PER.g_cfg_unusedPerFunctions) !== 'undefined' && typeof (NUTOOL_PER.g_cfg_unusedPerFunctions[partNumber]) !== 'undefined') {
			if (typeof (NUTOOL_PER.g_cfg_unusedPerFunctions[partNumber]) == "function") {
				unusedObjectArray.push(NUTOOL_PER.g_cfg_unusedPerFunctions[partNumber]());
			}
			else {
				unusedObjectArray.push(NUTOOL_PER.g_cfg_unusedPerFunctions[partNumber]);
			}
		}
		if (g_partNumber_type !== "" &&
			typeof (NUTOOL_PER.g_cfg_unusedPerFunctions) !== 'undefined' &&
			typeof (NUTOOL_PER.g_cfg_unusedPerFunctions[g_partNumber_type]) !== 'undefined') {
			if (typeof (NUTOOL_PER.g_cfg_unusedPerFunctions[g_partNumber_type]) == "function") {
				unusedObjectArray.push(NUTOOL_PER.g_cfg_unusedPerFunctions[g_partNumber_type]());
			}
			else {
				unusedObjectArray.push(NUTOOL_PER.g_cfg_unusedPerFunctions[g_partNumber_type]);
			}
		}
	
		for (var h = 0; h < unusedObjectArray.length; h += 1) {
			unusedObject = unusedObjectArray[h];
			unusedObjectProperties = getPropertyNames(unusedObject);
			for (var i = 0; i < unusedObjectProperties.length; i += 1) {
				if (unusedObjectProperties[i] === 'SUBSTRING') {
					// trim the unused GPIO function for the specific module
					for (var j = 0, maxJ = unusedObject[unusedObjectProperties[i]].length; j < maxJ; j = j + 1) {
						unusedPerFunction = unusedObject[unusedObjectProperties[i]][j];
						for (var k = perFunctions.length - 1; k >= 0; k -= 1) {
							if (perFunctions[k].indexOf(unusedPerFunction) === 0) {
								perFunctions.splice(k, 1);
							}
						}
					}
				}
				else if (unusedObjectProperties[i] === 'FULL') {
				// trim the unused GPIO function for the specific module
					for (j = 0, maxJ = unusedObject[unusedObjectProperties[i]].length; j < maxJ; j = j + 1) {
						unusedPerFunction = unusedObject[unusedObjectProperties[i]][j];
						if (unusedPerFunction.indexOf('.txt') === -1) {
							unusedPerFunction = unusedPerFunction + '.txt';
						}
						for (k = perFunctions.length - 1; k >= 0; k -= 1) {
							if (perFunctions[k] === unusedPerFunction) {
							perFunctions.splice(k, 1);
							}
						}
					}
				}
				else {
					if (!g_bReadyForRelease) { alert('A problem occurred when trimming unused perFunctions.'); }
				}
			}
		}

		return perFunctions.sort();
	}

	const onSelectPeripheralFunc = (selectedKeys, e) => {
		if(e.node.checked){
			// r_snippetName
			dispatch(setSnippetName(e.node.title));
		}
		else {
			return;
		}
	}

	const onCheckPeripheralFunc = (checkedKeys, e) => {
		//let childrenNode = e.node.children;
		let bCheck = e.checked;
		let { title, key }= e.node;

		if(e.node.className === "custom-ant-tree-radio") {
		  // Only one perioheral can be selected
			checkedKeys = checkedKeys.filter(tmp => 
				tmp.split('_')[0] !== title.split('_')[0] || tmp === key
			);
		}
		dispatch(set_cfg_perFunctions(checkedKeys));
		dispatch(setSnippetName(bCheck ? title : ''));
	}

	const createPeripheralFuncTree = () => {
		var perFunctions = getUsablePeripheralFunc();
		var treeData = [];
		for(var i=0; i<perFunctions.length; i++){
			let fileName = perFunctions[i];
			let snippetName = fileName.split('.')[0];
			let tmpNode = {
				title: snippetName,
				key: fileName,
			};

			// EX: 'PDMA_CH0.txt'
			if(fileName.includes('_')){
				let parentName = fileName.split('_')[0];
				let matchIndex = treeData.findIndex(data => data.title === parentName);
				// [Workaround] Change its css style
				if(NUTOOL_PER.g_cfg_perFuncMutex.includes(parentName)){
					tmpNode.className = "custom-ant-tree-radio";
				}
		
				// EX: No parentNode
				if(matchIndex === -1){
					treeData.push({
						title: parentName,
						key: parentName,
						icon: <DatabaseFilled className = "custom-ant-tree-icon"/>,
						checkable: false,
						children: [tmpNode],
					})
				}
				else{
					treeData[matchIndex].children.push(tmpNode);
				}
			}
			// EX: CLK.txt
			else{
				treeData.push(tmpNode);
			}
		}

		try {
			return (
				<Tree
					showLine
					showIcon
					checkable
					defaultExpandedKeys={r_cfg_perFunctions}
					defaultSelectedKeys={r_cfg_perFunctions}
					defaultCheckedKeys={r_cfg_perFunctions}
					checkedKeys={r_cfg_perFunctions}
					onSelect={onSelectPeripheralFunc}
					onCheck={onCheckPeripheralFunc}
					treeData={treeData}
				/>
			)
		} catch(e) {}
	}

	return (
		<Layout>
			<Header className='peripheralFunctions-header'>
				<b>Peripheral Functions</b>
			</Header>
			<Content className='peripheralFunctions-content'>
				{createPeripheralFuncTree()}
			</Content>
		</Layout>
	);
};

export default PeripheralFunctions;