import React, { Component } from 'react'
import {
  Layout,
} from 'antd';

import './App.css';

import ChipSeries from './components/ChipSeries';
import PartNo from './components/PartNo';
import PeripheralFunctions from './components/PeripheralFunctions';
import LoadPeripheralTxt from './components/LoadPeripheralTxt';

const { Header, Sider, Content } = Layout;

class App extends Component {
  render() {
    return (
      <Layout >
        <Sider >
          <Header className='layout-silder-header'>
            <ChipSeries/>
            <PartNo/>
          </Header>
          <Content>
            <PeripheralFunctions/>
          </Content>
        </Sider>
        <Content className='layout-content'>
          <LoadPeripheralTxt/>
        </Content>
      </Layout>
    );
  }
}

export default App;
