// g_chipType
import { createSlice } from "@reduxjs/toolkit";
import { setGlobalVariables } from "../../content-web-commonLib";

const initialState = g_chipTypes[0]; // "M251";

const r_chipType = createSlice({
    name: "r_chipType",
    initialState,
    reducers: {
        setChipType(state, action) {
            state = action.payload;
            setGlobalVariables("g_chipType", state);
            return state;
        }
    }
});

export const { setChipType } = r_chipType.actions;
export default r_chipType.reducer;
